/* === NAVIGATION === */
$nav-z-base: 400;

header.banner {
	z-index: $nav-z-base + 2;
	position: fixed;
	width: 100%;
	padding: .5rem;
	background: $black;
	.wrap {
		width: 100%;
		padding: 5px;
		display: flex;
		flex-direction: row;
		flex-wrap: nowrap;
		justify-content: space-between;
		align-items: center;
		color: $white; }
	.wrap::after {
		content: none; } }

.nav-primary ul.nav {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	li {
		margin: 10px;
		a {
			color: $white;
			&:hover {
				color: $brand-primary;
				transition: color .25s; } } } }

a.brand {
	font-family: $trade-gothic;
	font-weight: light;
	text-transform: uppercase;
	font-size: 1.2rem;
	margin-bottom: 0px;
	line-height: 1;
	color: $white;
	strong {
		font-weight: bolder;
		margin-left: .15rem; } }
.brand-tagline {
	font-family: $trade-gothic;
	font-size: .9rem;
	text-transform: lowercase;
	margin-bottom: 0px; }

a.brand:hover {
	text-decoration: none;
	color: $brand-primary;
	transition: all .25s; }


.social-icons a {
	background-color: transparent;
	color: $white;
	&:hover {
		color: $brand-primary;
		transition: all .25s;
		background-color: transparent; } }

a.brand {
	width: auto !important;
	padding-top: 0px; }

