header.banner .wrap {
		display: flex;
		justify-content: space-between;
		align-items: center;
		flex-direction: row;
		flex-wrap: nowrap;
		max-width: 100%;
		padding: 2px;
		@include breakpoint($large) {
			// +padding-sides(60px)
 }			// justify-content: center
		opacity: .99;
		// z-index: 4

		nav.nav-primary {
			display: flex;
			justify-content: space-between;
			@include breakpoint($large) {
				justify-content: center; }
			align-items: center;
			flex-direction: row;
			flex-wrap: wrap;
			color: $white;
			order: 1;
			@include breakpoint($large) {
				order: 2;
				flex-grow: 2; }
			width: auto;
			li a span, li a {
				text-decoration: none !important;
				color: $white; }

			li:not([class*="whomadethis-lightbox"]) {
				display: none;
				@include breakpoint($large) {
					display: inline-block; } }

			li.whomadethis-lightbox a:before {
				content: '\f142';
				font-family: "Ionicons";
				font-size: 3rem;
				@include breakpoint($large) {
					display: none; } }
			li.whomadethis-lightbox a span {
				display: none;
				@include breakpoint($large) {
					display: inline-block; } } }

		a.brand {
			order: 2;
			flex-grow: 2;
			@include breakpoint($large) {
				order: 1;
				flex-grow: 0; } }
		.social-icons {
			width: auto;
			display: flex;
			flex-wrap: row;
			flex-wrap: nowrap;
			order: 3;
			a {
				display: inline-block;
				// background-color: $white
				margin: 5px;
				text-align: center;
				border-radius: 2px;
				color: $white;
				i {
					color: $white; }
				&:hover {
					color: $white;
					@include transition(.3s); } }
			a:not([class*="youtube"]) {
				display: none;
				@include breakpoint($large) {
					display: inline-block; } } } }

::-webkit-scrollbar {
	display: none;
	@include breakpoint($large) {
		display: auto; } }

.nav-secondary {
	box-shadow: $shadow;
	z-index: 4;
	position: relative;
	background-color: $white;

	.nav-filter-overlay {
		position: absolute;
		pointer-events: none;
		right: 0;
		bottom: 0;
		width: 50%;
		height: 80px;
		background: linear-gradient(left, rgba(255,255,255,0) 0%, rgba(255,255,255,0) 0%, rgba(255,255,255,0.71) 71%, rgba(255,255,255,1) 100%);
		@include breakpoint($large) {
			display: none; }
 }		// background: $red

	.wrap {
		width: auto;
		overflow: auto;
		width: 100%;
		max-width: 100%;
		@include padding-sides(20px);

		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		@include breakpoint($med) {
			flex-direction: row;
			flex-wrap: nowrap;
			justify-content: space-between;
			align-items: center; }

		.menu-filter-container {
			width: 100%;
			list-style-type: none;
			overflow-x: scroll;
			white-space: nowrap;
			@include breakpoint($med) {
				width: auto;
				height: 80px; }
			flex-grow: 1;
			order: 2;
			li {
				display: inline-block;
				width: auto; }
			li a {
				display: inline-block;
				width: auto;
				text-transform: lowercase;
				text-decoration: none;
				margin: 20px 4px;
				padding: 10px 20px;
				border-radius: 2px;
				&:hover {
					color: $yellow; } }
			li a.selected {
				background-color: $yellow;
				color: $white !important; } }

		.search-bar-wrap {
			text-align: right;
			display: inline-block;
			width: 100%;
			order: 1;
			margin-top: 20px;
			// margin-bottom: 10px
			white-space: nowrap;
			max-height: 100%;
			@include transition(.2s);
			@include breakpoint($med) {
				margin-top: 2px;
				width: 30%;
				order: 3;
				margin-right: 10px; }
			.quicksearch {
				width: 100%;
				display: inline-block;
				margin: 2px;
				height: 40px;
				padding: 2px 10px;
				border-radius: 2px;
				border: transparent;
				background: rgba(0,0,0,.22);
				font-family: Avenir;
				font-size: 16px;
				color: #4a4a4a; } }

		.search-bar-wrap.small {
			max-height: 0px;
			margin-top: 0px;
			@include transition(.2s); }
		#clear-search {
			display: inline-block;
			position: relative;
			padding-top: 6px;
			right: 25px;
			@include breakpoint($med) {
				right: 35px; }
			color: rgba(0,0,0,.4); } } }

#sticky-wrapper.is-sticky {
	.nav-secondary {
		margin-top: 60px;
		margin-bottom: 100px; } }


.econstories-mailchimp-signup-widget {
	background: $yellow;
	width: 100%;

	form {
		width: 100%;
		text-align: center;
		input, label {
			width: 90%;
			margin: auto;
			display: block;
			font-size: 1.2rem; }
		label {
			text-align: center;
			padding: 30px;
			color: $white; }
		input {
			border-radius: $border-radius;
			background: rgba(255,255,255,.3);
			border: transparent;
			max-width: 600px;
			display: inline-block;
			height: 50px;
			padding-left: 15px;
			color: $white; }
		input[type="submit"] {
			content: '\f10a';
			font-family: "Ionicons";
			font-size: 1.5rem;
			width: 40px;
			background: transparent;
			position: relative;
			color: $white; }
		input:not([type="submit"]) {
			margin-right: -50px;
			margin-bottom: 40px;
			font-size: 1.2rem;
			font-weight: 400; }
		::-webkit-input-placeholder {
			color: $white;
			font-size: 1.2rem;
			font-weight: 400;
			padding-left: 10px; } } }
