.sr-only, .screen-reader-text {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .screen-reader-text:active, .sr-only-focusable:focus, .screen-reader-text:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

body {
  font-weight: normal;
  font-size: 95%;
}

@media (min-width: 640px) {
  body {
    font-size: 100%;
  }
}

p {
  font-weight: normal;
  font-size: 95%;
}

@media (min-width: 640px) {
  p {
    font-size: 100%;
  }
}

.entry-title,
.entry-title a {
  font-family: "Trade Gothic W01";
  font-size: 1.4em;
  font-weight: normal;
  text-transform: uppercase;
  text-decoration: none;
  font-weight: bold;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

a:focus {
  color: #E6BC68 !important;
  outline: none;
  text-decoration: none;
}

/* === COMPONENTS === */
.comment-list {
  padding-left: 0;
  list-style: none;
}

.comment-list ol {
  list-style: none;
}

.search-form label {
  font-weight: normal;
}

/* === NAVIGATION === */
header.banner {
  z-index: 402;
  position: fixed;
  width: 100%;
  padding: .5rem;
  background: #1C1C1C;
}

header.banner .wrap {
  width: 100%;
  padding: 5px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  color: #FFFFFF;
}

header.banner .wrap::after {
  content: none;
}

.nav-primary ul.nav {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}

.nav-primary ul.nav li {
  margin: 10px;
}

.nav-primary ul.nav li a {
  color: #FFFFFF;
}

.nav-primary ul.nav li a:hover {
  color: #E6BC68;
  transition: color .25s;
}

a.brand {
  font-family: "Trade Gothic W01";
  font-weight: light;
  text-transform: uppercase;
  font-size: 1.2rem;
  margin-bottom: 0px;
  line-height: 1;
  color: #FFFFFF;
}

a.brand strong {
  font-weight: bolder;
  margin-left: .15rem;
}

.brand-tagline {
  font-family: "Trade Gothic W01";
  font-size: .9rem;
  text-transform: lowercase;
  margin-bottom: 0px;
}

a.brand:hover {
  text-decoration: none;
  color: #E6BC68;
  transition: all .25s;
}

.social-icons a {
  background-color: transparent;
  color: #FFFFFF;
}

.social-icons a:hover {
  color: #E6BC68;
  transition: all .25s;
  background-color: transparent;
}

a.brand {
  width: auto !important;
  padding-top: 0px;
}

header.banner .wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  max-width: 100%;
  padding: 2px;
  opacity: .99;
}

header.banner .wrap nav.nav-primary {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  color: #FFFFFF;
  order: 1;
  width: auto;
}

@media (min-width: 900px) {
  header.banner .wrap nav.nav-primary {
    justify-content: center;
  }
}

@media (min-width: 900px) {
  header.banner .wrap nav.nav-primary {
    order: 2;
    flex-grow: 2;
  }
}

header.banner .wrap nav.nav-primary li a span, header.banner .wrap nav.nav-primary li a {
  text-decoration: none !important;
  color: #FFFFFF;
}

header.banner .wrap nav.nav-primary li:not([class*="whomadethis-lightbox"]) {
  display: none;
}

@media (min-width: 900px) {
  header.banner .wrap nav.nav-primary li:not([class*="whomadethis-lightbox"]) {
    display: inline-block;
  }
}

header.banner .wrap nav.nav-primary li.whomadethis-lightbox a:before {
  content: '\f142';
  font-family: "Ionicons";
  font-size: 3rem;
}

@media (min-width: 900px) {
  header.banner .wrap nav.nav-primary li.whomadethis-lightbox a:before {
    display: none;
  }
}

header.banner .wrap nav.nav-primary li.whomadethis-lightbox a span {
  display: none;
}

@media (min-width: 900px) {
  header.banner .wrap nav.nav-primary li.whomadethis-lightbox a span {
    display: inline-block;
  }
}

header.banner .wrap a.brand {
  order: 2;
  flex-grow: 2;
}

@media (min-width: 900px) {
  header.banner .wrap a.brand {
    order: 1;
    flex-grow: 0;
  }
}

header.banner .wrap .social-icons {
  width: auto;
  display: flex;
  flex-wrap: row;
  flex-wrap: nowrap;
  order: 3;
}

header.banner .wrap .social-icons a {
  display: inline-block;
  margin: 5px;
  text-align: center;
  border-radius: 2px;
  color: #FFFFFF;
}

header.banner .wrap .social-icons a i {
  color: #FFFFFF;
}

header.banner .wrap .social-icons a:hover {
  color: #FFFFFF;
}

header.banner .wrap .social-icons a:not([class*="youtube"]) {
  display: none;
}

@media (min-width: 900px) {
  header.banner .wrap .social-icons a:not([class*="youtube"]) {
    display: inline-block;
  }
}

::-webkit-scrollbar {
  display: none;
}

@media (min-width: 900px) {
  ::-webkit-scrollbar {
    display: auto;
  }
}

.nav-secondary {
  box-shadow: 0px 0px 11px rgba(0, 0, 0, 0.1);
  z-index: 4;
  position: relative;
  background-color: #FFFFFF;
}

.nav-secondary .nav-filter-overlay {
  position: absolute;
  pointer-events: none;
  right: 0;
  bottom: 0;
  width: 50%;
  height: 80px;
  background: linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.71) 71%, white 100%);
}

@media (min-width: 900px) {
  .nav-secondary .nav-filter-overlay {
    display: none;
  }
}

.nav-secondary .wrap {
  width: auto;
  overflow: auto;
  width: 100%;
  max-width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

@media (min-width: 640px) {
  .nav-secondary .wrap {
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
  }
}

.nav-secondary .wrap .menu-filter-container {
  width: 100%;
  list-style-type: none;
  overflow-x: scroll;
  white-space: nowrap;
  flex-grow: 1;
  order: 2;
}

@media (min-width: 640px) {
  .nav-secondary .wrap .menu-filter-container {
    width: auto;
    height: 80px;
  }
}

.nav-secondary .wrap .menu-filter-container li {
  display: inline-block;
  width: auto;
}

.nav-secondary .wrap .menu-filter-container li a {
  display: inline-block;
  width: auto;
  text-transform: lowercase;
  text-decoration: none;
  margin: 20px 4px;
  padding: 10px 20px;
  border-radius: 2px;
}

.nav-secondary .wrap .menu-filter-container li a:hover {
  color: #E6BC68;
}

.nav-secondary .wrap .menu-filter-container li a.selected {
  background-color: #E6BC68;
